import "./styles.css";
import Flexsearch from "flexsearch";
import { grapes } from "./data";
import React from "react";
import didYouMean, { ReturnTypeEnums } from "didyoumean2";

const index = new Flexsearch.Index({
  charset: "latin:advanced",
  tokenize: "reverse",
  cache: true
});

const splitToNames = (grapeName) => {
  const names = grapeName
    .replace(",", "/")
    .replace("(", "/")
    .replace(")", "/")
    .split("/")
    .map((n) => n.trim());

  let partialNames = [];

  names.forEach((name) => {
    if (name.split(" ").length > 0) {
      partialNames.push(name.split(" "));
    }
  });

  return names.concat(partialNames.flat());
};
const data = grapes;
let data2 = [];
data.forEach((d, i) =>
  data2.push(splitToNames(d).map((s) => ({ key: s, index: i })))
);
data2 = data2.flat().filter((d) => !!d.key);

data.forEach((grape, i) => {
  index.add(i, grape);
});

export default function App() {
  const [term, setTerm] = React.useState("");
  const [results, setResults] = React.useState([]);
  const [suggestions, setSuggestions] = React.useState([]);
  React.useEffect(() => {
    setResults(index.search(term, 25, { suggest: true }));
    const sugg = didYouMean(term, data2, {
      matchPath: ["key"],
      returnType: ReturnTypeEnums.ALL_SORTED_MATCHES
    });
    let indexes = [];
    const filtered = sugg.filter((s) => {
      if (indexes.includes(s.index)) return false;
      indexes.push(s.index);
      return true;
    });

    setSuggestions(filtered);
  }, [term]);

  return (
    <div
      className="App"
      style={{ height: "100vh", display: "flex", flexDirection: "column" }}
    >
      <div style={{ flex: "1 1 auto", overflow: "auto" }}>
        <h6>Ősz Gyula szőlőlistája alapján</h6>
        <h2>Kísérleti szőlő név kereső:</h2>

        <input
          type="text"
          value={term}
          onChange={(e) => setTerm(e.target.value)}
        />
        <h4>Keresési eredmények</h4>
        {results.slice(0, 10).map((result) => (
          <div key={result}>{data[result]} </div>
        ))}
        <h4>Ajánlások</h4>
        {suggestions.slice(0, 10).map((suggestion) => (
          <div key={suggestion.index}>{data[suggestion.index]} </div>
        ))}
      </div>

      <div
        style={{
          width: "100%",
          marginTop: "20px"
        }}
      >
        <div>
          Csupa nagybetűs magyar név esetén a csemegeszőlők.hu-n megtalálható a
          szőlő leírása magyarul. Egyébként az orosz névre lehet rákeresni a
          виноград (szőlő) szót hozzátéve az orosz névhez. Az így megtalált
          orosz nyelvű oldalt a Chrome böngésző automatikusan lefordítja. Vagy
          bemásolhatjuk a lefordítani kívánt szöveget a https://www.deepl.com/
          oldalra, ami a google fordítónál egy picit jobb eredményt szokott
          adni.
        </div>
        <div style={{ background: "black", color: "white", padding: "10px" }}>
          © Ambruzs Péter 2021
        </div>
      </div>
    </div>
  );
}
